import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./navbar.css";
import Robo from '../img/robo.png'

function NavBar() {
  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);
  return (
    <>
      <nav className="navbar">
        <div className="nav-container">
          <NavLink exact to="/" className="nav-logo">
            <p style={{marginBottom:'-15px',fontFamily:'Kanit',fontWeight:'600',fontSize:'45px',marginLeft:'0px'}}><img src={Robo} height="60px" width='auto'></img>
            </p>
            
            <i className="fas fa-code"></i>
          </NavLink>

          <ul className={click ? "nav-menu active" : "nav-menu"}>
            <li className="nav-item">
              <NavLink
                exact
                to="/"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
                style = {{fontFamily:'Kanit',fontWeight:'600'}}
              >
                Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/Lesson"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
                style = {{fontFamily:'Kanit',fontWeight:'600'}}
              >
                Lesson
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/Addlession"
                activeClassName="active"
                className="nav-links"
                style = {{fontFamily:'Kanit',fontWeight:'600'}}
                onClick={handleClick}
              >
                Addlession
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/Login"
                activeClassName="active"
                className="nav-links"
                style = {{fontFamily:'Kanit',fontWeight:'600'}}
                onClick={handleClick}
              >
                Login
              </NavLink>
            </li>
          </ul>
          <div className="nav-icon" onClick={handleClick}>
            <i className={click ? "fas fa-times" : "fas fa-bars"}></i>
          </div>
        </div>
      </nav>
    </>
  );
}

export default NavBar;
